import './submit.css'
function Submit(){
    return(
        <>
        <div className='submit-box'>
            <div className="right-data">
                <label className="submitcourseheader">Master in Full-Stack Development (MERN Stack)</label>
                <p className="submitcourseoverview">
Review

As the world is increasingly turning to digital space for businesses, designing web applications and websites is going to be the future. A skilled full-stack developer will be an asset to any web development team.</p>
            </div>
            {/* <div className="submit-box"> */}
                <form action="" className='submit-form-box'>
                    <label className="registerheadingname"> Registration</label>
                    <div className="form-begin">
                        <span className="submitinputheading">First Name</span>
                        <input type="text" className="submitformbox" />
                        <span className="submitinputheading">Last name</span>
                        <input type="text" className="submitformbox" />
                        <span className="submitinputheading">Email</span>
                        <input type="text" className="submitformbox" />
                        <span className="submitinputheading">Phone Number</span>
                        <input type="text" className="submitformbox" />
                        <button className='formsubmitbtn'>Apply Now</button>
                    </div>
                </form>
            {/* </div> */}
        </div>
        </>
    )
}
export default Submit;